/* color palette:
#FFFFFF - white
#C1CAD6 - grey
#6C6EA0 - lavender
#66C7F4 - sky blue
#FF1053 - deep pink
*/
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto Mono', monospace;
}
input {
  width: 90%;
  margin: 5%;
  padding-left: 1%;
  font-size: 2em;
}
.container {
  width: 40%;
  margin: 5% 30%;
  background-color: #C1CAD6;
}
.calculator {
  width: 90%;
  margin-left: 5%;
  padding: 2.5% 0 5%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 8px;
}
.btn {
  width: 100%;
  padding: 5%;
  font-size: 1em;
  border: none;
  border-radius: 5px;
  background-color: #FFFFFF;
  outline: none;
}
.btn:hover {
  background-color: #F3F3F3;
}
.btn:active {
  transform: translateY(1px);
}
.extra {
  background-color: #6C6EA0;
  color: #FFFFFF;
}
.extra:hover {
  background-color: #616390;
}
.btn[disabled], #clear[disabled], #clear[disabled]:hover {
  background-color: #ffe7ed;
  color: #C1CAD6;
}
#power:active, #clear:active {
  transform: translateY(1px);
}
